.Service{
    display: flex;
    height: 90vh;
    flex: 1;
    gap: 1rem;
}

.s-leftside{
    margin-top: 5rem;
    display: flex;
    flex: 0.5;
    position: relative;
    flex-direction: column;
    gap: 1rem;
}

.s-detail{
    display: flex;
    position: relative;
    flex-direction: column;
    gap: 0.5rem;
}

.s-detail>:nth-child(1){
    color: var(--black);
    font-weight: bold;
    font-size: 2rem;
}

.s-detail>:nth-child(2){
    color: var(--xanhduongdamvl);
    font-weight: bold;
    font-size: 2rem;
}

.s-detail>:nth-child(3){
    color: var(--gray);
    font-family: tahoma;
    font-size: 18px;
}

.book{
    width: 200px;
}

.s-button{
    width: 200px;
}

.s-rightside{
    flex: 1;
    position: relative;
}

.s-rightside>*{
    position: absolute;
}

.s-blur{
    background: rgb(238, 210, 255);
    position: absolute;
    width: 30rem;
    height: 20rem;
    border-radius: 50%;
    filter: blur(72px);
    z-index: -9;
    top: 18%;
    left: 45%;
}