.n-wrapper{
    height: 10vh;
    display: flex;
    justify-content: space-between;
}

.n-left{
    display: flex;
    flex: 1;
    align-items: center;
    gap: 2rem;
}

.n-name{
    font-size: 1.3rem;
    font-weight: bold;
    color: var(--xanhduongdamvl);
}

.n-right{
    display: flex;
    flex: 1;
    align-items: center;
    justify-content: center;
    font-weight: 400;
}

.n-list{
    flex: 1;
}

.n-list>ul{
    display: flex;
    flex: 1;
    justify-content: center;
    gap: 2rem;
    margin-right: 4rem;
}

.n-list>ul>li:hover{
    cursor: pointer;
    color: var(--xanhduongnhat);
    
}

.n-button{
    width: 5.5rem;
}
