:root
{
    --xanhduongnhat : #74b9ff;
    --xanhduongdamvl : #0984e3;
    --yellow: #F5C32C;
    --orange : #FCA61F;
    --black : #242D49;
    --gray : #788097;
    --blueCard : #DDF8FE;
    --purple: rgb(238 210 255);
    --boxShadow : 0px 19px 60px rgb(0 0 0 / 8%);
    --orangeCard: rgba(252, 166, 31, 0.45);
    --smboxShadow: -79px 51px 60px rgba(0, 0, 0, 0.08);
}

.App{
    padding: 0.5rem 3.5rem;
    overflow: hidden;
    color: var(--black);    
}

.button{
   border-radius: 15px;
   border: none;
   color: white;
   font-size: 16px;
   padding: 10px;
   background: linear-gradient(90deg, rgba(104,165,228,1) 25%, rgba(116,185,255,1) 100%);
}

.button:hover{
    cursor: pointer;
    color: var(--xanhduongdamvl);
    background: white;
    box-shadow: 0px 0px 10px 1px var(--xanhduongdamvl); 
}

.card:hover{
    box-shadow: 0px 0px 10px 1px var(--xanhduongnhat); 
}
  

  