.Floating-Div{
    display: flex;
    justify-content: space-around;
    background: white;
    border-radius: 17px;
    align-items: center;
    height: 4.5rem;
    padding: 0px 20px 0px 0px;
    opacity: 80%;
}

.Floating-Div:hover{
    opacity: 100%;
}

.Floating-Div>img{
    transform: scale(0.6);
}

.Floating-Div>span{
    font-family: tahoma;
    font-size: 18px;
}