.intro{
    display: flex;
    height: 75vh;
    margin-top: 6rem;
}

.i-left{
    display: flex;
    flex: 1;
    position: relative;
    flex-direction: column;
    gap: 2.5rem;
}

.i-name{
    display: flex;
    flex-direction: column;
}

.i-name>:nth-child(1){
    color: var(--black);
    font-weight: bold;
    font-size: 3rem;
}

.i-name>:nth-child(2){
    color: var(--xanhduongnhat);
    font-weight: bold;
    font-size: 3rem;
}

.i-name>:nth-child(3){
    color: var(--gray);
    font-family: tahoma;
    font-weight: 400;
    margin-top: 15px;
}

.i-button{
    width: 150px;
}

.i-icons{
    margin-top: 25px;
    display: flex;
}

.i-icons>*{
    margin-top: 10px;
    transform: scale(0.5);
    gap: 1rem;
}

.i-right{
    flex: 1;
    position: relative;
}

.i-right>*{
    position: absolute;
    z-index: 1;
}

.i-right>:nth-child(1){
    transform: scale(1.3);
    top: -9%;
    left: +15%;
}

.i-right>:nth-child(2){
    transform: scale(1.5, 1.35);
    top: -9%;
    left: +19.5%;
}

.i-right>:nth-child(3){
    transform: scale(1.5);
    top: +8%;
    left: +25%;
}

.i-right>:nth-child(4){
    transform: scale(0.75);
    top: -15%;
    left: -8%;
    border-radius: 50%;
}

.blur-div{
    background: rgb(238, 210, 255);
    position: absolute;
    width: 22rem;
    height: 14rem;
    border-radius: 50%;
    filter: blur(72px);
    z-index: -9;
    top: -13%;
    left: 53%;
}