.card{
    display: flex;
    flex-direction: column;
    height: 13rem;
    width: 11rem;
    align-items: center;
    text-align: center;
    background: rgba(255, 255, 255, 0.3);
    border: 5px solid var(--xanhduongnhat);
    border-radius: 17px;
    position: relative;
}

.card>:nth-child(1){
    margin-bottom: -0.6rem;

}

.card>:nth-of-type(2){
    color: var(--gray);
    font-size: 16px;
}

.card>img{
    transform: scale(0.7,0.65);
    margin-bottom: -3rem;
    margin-top: -1.1rem;
    border-radius: 50%;
}

.c-button{
    margin-top: 1.1rem;
    background: white;
    color: var(--xanhduongdamvl);
}


